import React, {
	forwardRef,
} from "react";

import LinkButton from "../Button/LinkButton";
import {
	buildAriaLabel,
	buildRequirements,
	ComponentWrapper,
	LinkButtonWrapper } from "../Input";
import Label from "../Label/Label";

import { StyledTextArea, TextAreaWrapper } from "./TextArea.styles";
import { TextAreaProps } from "./TextArea.types";


const TextArea = forwardRef<HTMLTextAreaElement, TextAreaProps>(({
	dataQA,
	id,
	label,
	required = false,
	disabled,
	requirements,
	$resize = "none",
	rows,
	topButtonLinkText,
	topButtonLinkAction,
	onChange,
	validation,
	mt,
	ml,
	mr,
	mb,
	m,
	className,
	autoComplete = "off",
	value,
	...props
}, ref) => (
	<ComponentWrapper
		mt={mt}
		mb={mb}
		ml={ml}
		mr={mr}
		m={m}
		data-qa={dataQA}
		className={className}
		aria-required={required}
	>
		{label && (
			<Label
				dataQA={`${dataQA}-label`}
				isInvalid={validation === "invalid"}
				htmlFor={id}
				id={`${dataQA}-label`}
				isRequired={required}
			>
				{label}
			</Label>
		)}
		{topButtonLinkText && (
			<LinkButtonWrapper>
				<LinkButton onClick={topButtonLinkAction} dataQA={`${dataQA}-buttonLink`} buttonText={topButtonLinkText} />
			</LinkButtonWrapper>
		)}
		<TextAreaWrapper validation={validation} disabled={disabled}>
			<StyledTextArea
				ref={ref}
				{...props}
				required={required}
				value={value}
				aria-labelledby={buildAriaLabel(label, requirements, dataQA)}
				data-qa={`${dataQA}-text-area`}
				id={id}
				key={id}
				validation={validation}
				aria-invalid={validation === "invalid"}
				autoComplete={autoComplete}
				$resize={$resize}
				rows={rows}
				onChange={onChange}
				disabled={disabled}
			/>
		</TextAreaWrapper>
		{requirements && buildRequirements(requirements, dataQA, id, validation)}
	</ComponentWrapper>
));
TextArea.displayName = "TextArea";
export default TextArea;
